<template>
  <main class="content container" v-if="productLoading">
    <div class="" style="text-align:center">
      <img src="	https://thumbs.gfycat.com/ExcellentIdleHaddock-size_restricted.gif" alt="Загрузка товара">
      <p>Информация о товаре загружается</p>
    </div>
  </main>
  <main class="content container" v-else-if="!productData">
    <div class="" style="text-align:center">
      <img src="https://i.gifer.com/origin/21/21bb299fb1821a2f1bff99ccf1097bf2_w200.gif" alt="ошибка загрузки" style="width: 200px; height: 300px; margin: 0.75px 0px;">
      <p>Произошла ошибка загрузки товара</p>
    <button class="button button--primery" style="width" @click.prevent="loadProduct">Попробовать снова</button>
  </div></main>
    <main class="content container" v-else>
        <div class="content__top">
          <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'main'}" > Каталог </router-link >
            </li>
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'main'}"> {{ category.title }} </router-link>
            </li>
            <li class="breadcrumbs__item">
              <a class="breadcrumbs__link"> {{ product.title }} </a>
            </li>
          </ul>
        </div>
  
        <section class="item">
          <div class="item__pics pics">
            <div class="pics__wrapper">
              <div class="" v-for="(image, index) in product.colors" :key="index">
              <img
                width="570"
                height="570"
                v-if="image.color.id === value"
                :src="image.gallery[0].file.url" :alt="product.title"
              />
              </div>
              
            </div>
            <ul class="pics__list">
                <li class="pics__item" v-for="(colors, index) in productData.colors" :key="index" >
                  <a href="#" class="pics__link ">
                    <img
                      @click.prevent="changeValueColor(product.colors[index].color.id)"
                      width="98"
                      height="98"
                      :src="product.colors[index].gallery[0].file.url" :alt="product.title"
                    />
                  </a>
                </li>
            </ul>
          </div>
  
          <div class="item__info">
            <span class="item__code">Артикул: {{product.id}}</span>
            <h2 class="item__title">{{product.title}}</h2>
            <div class="item__form">
              <form class="form" action="#" method="POST" @submit.prevent="addToCart">
                <div class="item__row item__row--center">
                  <div class="form__counter">
                    <button type="button" aria-label="Убрать один товар" @click.prevent="productAmountDecrement">
                      <svg width="12" height="12" fill="currentColor">
                        <use xlink:href="#icon-minus"></use>
                      </svg>
                    </button>
  
                    <input type="text" v-model.number="productAmount" />
  
                    <button type="button" aria-label="Добавить один товар" @click.prevent="productAmountIncrement">
                      <svg width="12" height="12" fill="currentColor">
                        <use xlink:href="#icon-plus"></use>
                      </svg>
                    </button>
                  </div>
  
                  <b class="item__price"> {{ product.price | numberFormat }} ₽ </b>
                </div>
  
                <div class="item__row">
                  <fieldset class="form__block">
                    <legend class="form__legend">Цвет</legend>
                    <ul class="colors colors--black">
                      <li class="colors__item" v-for="color, index in productData.colors" :key="index">
                        <label class="colors__label">
                          <input
                            class="colors__radio sr-only"
                            type="radio"
                            name="color-item"
                            :value="product.colors[index].color.id" v-model="value"
                            
                          />
                          <span
                            class="colors__value"
                            :style="{'background-color': product.colors[index].color.code }" style="border: 1px solid black"
                          >
                          </span>
                        </label>
                      </li>
                      
                    </ul>
                  </fieldset>
  
                  <fieldset class="form__block">
                    <legend class="form__legend">Размер</legend>
                    <label class="form__label form__label--small form__label--select">
                      <select class="form__select" type="text" name="category" v-model.number="size">
                        <option :value="size" v-for="size in productData.sizes" :key="size.id">{{ size.title }} </option>
                      </select>
                    </label>
                  </fieldset>
                </div>
  
                <button class="item__button button button--primery" type="submit" :disabled="productAddSending">В корзину</button>
                <div class="" v-show="productAdded">Товар добавлен в корзину</div>
                <div class="" v-show="productAddSending">Добавляем товар в корзину ...</div>

              </form>
            </div>
          </div>
  
          <div class="item__desc">
            <ul class="tabs">
              <li class="tabs__item">
                <a class="tabs__link tabs__link--current"> Информация о товаре </a>
              </li>
              <li class="tabs__item">
                <a class="tabs__link" href="#"> Доставка и возврат </a>
              </li>
            </ul>
  
            <div class="item__content">
              <h3>Состав:</h3>
  
              <p v-for="material in productData.materials" :key="material.id" style="margin:0">
                {{ material.title }}
              </p>
  
              <h3>Уход:</h3>
  
              <p>
                Машинная стирка при макс. 30ºC короткий отжим<br />
                Гладить при макс. 110ºC<br />
                Не использовать машинную сушку<br />
                Отбеливать запрещено<br />
                Не подвергать химчистке<br />
              </p>
            </div>
          </div>
        </section>
      </main>
</template>

<script>
  
    import gotoPage from '@/helpers/gotoPage'
    import numberFormat from '@/helpers/numberFormat'
    import axios from 'axios'
    import {API_BASE_URL} from '@/config.js'
    import { mapActions } from "vuex";

    export default {
        data(){
          return {
            productAmount: 1,
            value: 0,
            size: 0,
            productData: null,
            productLoading: false,
            productLoadingFailed: false,

            productAdded: false,
            productAddSending: false,
          }
        },
        filters: {
          numberFormat
        },
        computed: {
          
          product(){
            return this.productData
          },
          category() {
            return this.productData.category
          },

        },
        methods: {
          gotoPage,
          changeValueColor(id) {
            return this.value = id;
          },
          productAmountIncrement(){
          return this.productAmount = this.productAmount + 1
          },
          productAmountDecrement(){
            if (this.productAmount > 1) {
              return this.productAmount = this.productAmount - 1
              }
          },
          ...mapActions(['addProductToCart']),
          addToCart() {
            this.productAdded = false,
            this.productAddSending = true,
            this.addProductToCart({productId: this.product.id, amount: this.productAmount, colorId: this.value, sizeId: this.size.id})
              .then(() => {
                this.productAdded = true;
                this.productAddSending = false;
              })
          },
          loadProduct(){
            this.productLoading = true;
            this.productLoadingFailed = false;
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
              axios.get(API_BASE_URL + '/api/products/' + this.$route.params.id)
              .then((response) => this.productData = response.data)
              .catch(() => this.productLoadingFailed = true)
              .then(()=> this.productLoading = false)
              .then(()=> this.size = this.productData.sizes[0])
              .then(()=> this.value = this.productData.colors[0].color.id)
             
            }, 0)
          },
        },

        // отслеживает изменение url 
        watch: {
          '$route.params.id': {
            handler(){
              this.loadProduct();
            },
            immediate: true,
          }
        }

    }
</script>