<template>
    <main class="content container">
        <div class="content__top">
          <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'main'}"> Каталог </router-link>
            </li>
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'cart'}"> Корзина </router-link>
            </li>
            <li class="breadcrumbs__item">
              <a class="breadcrumbs__link"> Оформление заказа </a>
            </li>
          </ul>
  
          <div class="content__row">
            <h1 class="content__title">Оформление заказа</h1>
          </div>
        </div>
  
        <section class="cart">
          <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
            <div class="cart__field">
              <div class="cart__data">
                <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя"/>
                <BaseFormText v-model="formData.address" title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес"/>
                <BaseFormText v-model="formData.phone" type="tel" title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон"/>
                <BaseFormText v-model="formData.email" title="Email" type="email" :error="formError.email" placeholder="Введи ваш Email"/>
                <BaseFormTextarea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment" placeholder="Ваши пожелания"/>
              </div>
  
              <div class="cart__options">
                <h3 class="cart__title">Доставка</h3>
                <ul class="cart__options options">
                  <li class="options__item" v-for="delivery in deliveryTypeId" :key="delivery.id" >
                    
                    <label class="options__label">
                      <input class="options__radio sr-only" type="radio" name="delivery" :value="delivery" v-model="deliveryId">
                      <span class="options__value">
                        {{ delivery.title }} <b>{{ delivery.price | numberFormat }}</b>
                      </span>
                    </label>
                  </li>
                  
                </ul>
  
                <h3 class="cart__title">Оплата</h3>
                <ul class="cart__options options">
                  <li class="options__item" v-for="payment in paymentTypeId" :key="payment.id">
                    <label class="options__label">
                      <input class="options__radio sr-only" type="radio" name="pay" :value="payment.id" v-model="paymentId">
                      <span class="options__value">
                        {{ payment.title }}
                      </span>
                    </label>
                  </li>
                </ul>
              </div>
            </div>
  
            <div class="cart__block">
              <ul class="cart__orders">
                <li class="cart__order" v-for="product in products" :key="product.productId">
                  <h3>{{product.product.product.title}} <br> (кол-во: {{ product.amount }} шт.)</h3>
                  <b>{{ (product.product.price * product.amount) | numberFormat }} ₽</b>
                  <span>Артикул: {{product.product.product.id}}</span>
                </li>
               
              </ul>
  
              <div class="cart__total">
                <p>Доставка: 
                  <b v-if="!deliveryId.price ">не указана</b>
                  <b v-else>{{deliveryId.price | numberFormat}} ₽</b>
                </p>
                <p v-if="!deliveryId.price ">Итого: <b>{{products.length}}</b> товара на сумму <b>{{ totalPrice   | numberFormat }} ₽</b></p>
                <p v-else>Итого: <b>{{products.length}}</b> товара на сумму <b>{{ (totalPrice + Number(deliveryId.price))  | numberFormat }} ₽</b></p>
              </div>
  
              <button class="cart__button button button--primery" type="submit">
                Оформить заказ
              </button>
            </div>
            <div class="cart__error form__error-block" v-if="formErrorMessage || deliveryError || paymentError">
              <h4>Заявка не отправлена!</h4>
              <p v-if="formErrorMessage">{{formErrorMessage}}</p>
              <p v-if="deliveryError">Ошибка в типе доставки: {{deliveryError}}</p>
              <p v-if="paymentError">Ошибка в типе оплаты: {{paymentError}}</p>

            </div>
            
          </form>
        </section>
      </main>
</template>
<script>

import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config.js';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
    
    export default {
        filters: {
          numberFormat
        },
        computed: {
        ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
      },
        components: { BaseFormText, BaseFormTextarea},
        data() {
            return {
                formData: {
                },
                formError: {
                },
                formErrorMessage: '',
                deliveryError: '',
                paymentError: '',
                deliveryTypeId:null,
                paymentTypeId:null,
                deliveryId:{},
                paymentId:{},

            }
        },
        methods: {
          delivery(){
          axios
            .get(API_BASE_URL + "/api/deliveries")
            .then(response => {
              this.deliveryTypeId = response.data;
            })          
          },
          payments(){
          axios
            .get(API_BASE_URL + "/api/payments", {
              params:{
                deliveryTypeId: 1
              }
            })
            .then(response => this.paymentTypeId = response.data)
          
          },
          order() {
            this.formError = {}
            this.formErrorMessage = ''
            this.paymentError = ''
            this.deliveryError = ''

            axios.post(API_BASE_URL + '/api/orders', {
              ...this.formData,
              deliveryTypeId: this.deliveryId.id,
              paymentTypeId: this.paymentId,
            }, {
              params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            })
            .then((response) => {
              this.$store.commit('resetCart');
              this.$store.commit('updateOrderInfo', response.data);
              this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
            })
            .catch((error) => {
              this.formError = error.response.data.error.request || {}
              this.formErrorMessage = error.response.data.error.message
              this.deliveryError = error.response.data.error.request.deliveryTypeId
              this.paymentError = error.response.data.error.request.paymentTypeId
            })
          }
        },
        created(){
          this.delivery();
          this.payments();
        }
    }
</script>