<template>
  
    <li class="cart__item product" >
      <div class="product__pic">
        <img
          width="570"
          height="570"
          :src="getImage[0].gallery[0].file.url" :alt="item.product.title"
        />
      </div>
        <h3 class="product__title">{{item.product.product.title}} <br> размер: {{ item.product.size.title }}</h3>
        <p class="product__info product__info--color" >
          Цвет:
            <span><i :style="{backgroundColor: getImage[0].color.code}"></i>{{getImage[0].color.title}}</span>
        </p>
        <span class="product__code"> Артикул: {{ item.product.product.id }} </span>

        <div class="product__counter form__counter">
          <button type="button" aria-label="Убрать один товар" @click.prevent="decrement">
            <svg width="10" height="10" fill="currentColor">
              <use xlink:href="#icon-minus"></use>
            </svg>
          </button>

          <input type="text" v-model.number="amount" name="count" />

          <button type="button" aria-label="Добавить один товар" @click.prevent="increment">
            <svg width="10" height="10" fill="currentColor">
              <use xlink:href="#icon-plus"></use>
            </svg>
          </button>
        </div>

        <b class="product__price"> {{ (item.amount * item.product.price) | numberFormat}} ₽ </b>

        <button
          class="product__del button-del"
          style="cursor:pointer"
          type="button"
          aria-label="Удалить товар из корзины"
          @click.prevent="deleteProduct(item.productId)"
        >
          <svg width="20" height="20" fill="currentColor">
            <use xlink:href="#icon-close"></use>
          </svg>
        </button>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

    export default {    
        
        filters: {numberFormat},
        props: ['item'],
        computed: {
          amount: {
            get() {
            return this.item.amount
            },
            set(value) {
            this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
            }
          },
          getImage() {
            return this.item.product.product.colors.filter(el => el.color.id === this.item.colorId)
          },
          // getSize() {
          //   return this.item
          // },
          
        },
        methods: {
          deleteProduct() {
                this.$store.dispatch('deleteCartProduct', {productId: this.item.productId})
            },
          // ...mapMutations({deleteProduct: 'deleteCartProduct'}),
          increment() {
                    this.amount = this.item.amount + 1
                },
          decrement() {
              if (this.item.amount > 1) {this.amount = this.item.amount - 1}    
          }
        }
    }
</script>