<template>
    <main class="content container">
        <div class="content__top">
          <div class="content__row">
            <h1 class="content__title">Каталог</h1>
            <span class="content__info"> {{ countProducts }} товара </span>
          </div>
        </div>
    
        <div class="content__catalog">
          <ProductFilter 
            :price-from.sync="filterPriceFrom" 
            :price-to.sync="filterPriceTo" 
            :category-id.sync="filterCategoryId" 
            :collection-id.sync="filterCollectionId" 
            :material-id.sync="filterMaterialId"
            :color-id.sync="filterColorId"/>
          <section class="catalog">
            <div class="" v-if="productsLoading" style="text-align:center">
              <img src="https://i.gifer.com/origin/ac/ac22d6c6c7e1efd44f2c09bab2e11374_w200.gif" alt="Одежда гифки, анимированные GIF изображения одежда - скачать гиф картинки  на GIFER" style="width: 200px; height: 300px; margin: 0.75px 0px;">
              <p>Загрузка товаров ...</p>
            </div>
            <div class="" v-if="productsLoadingFailed" style="text-align:center">
              <img src="https://i.gifer.com/origin/21/21bb299fb1821a2f1bff99ccf1097bf2_w200.gif" alt="Одежда гифки, анимированные GIF изображения одежда - скачать гиф картинки  на GIFER" style="width: 200px; height: 300px; margin: 0.75px 0px;">
              <p>Произошла ошибка, попробуйте снова</p>
              <button class="button button--primery" style="width" @click.prevent="loadProducts">Попробовать снова</button>
            </div>
            <ProductList 
                :products="products"
                />
            <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
          </section>  
        </div>
        
      </main>
</template>

<script>

import ProductList from '@/components/ProductList.vue'
import BasePagination from '@/components/BasePagination.vue'
import ProductFilter from '@/components/ProductFilter.vue'
import axios from 'axios'
import {API_BASE_URL} from '@/config.js'



export default {
  components: {
    ProductList: ProductList,
    BasePagination: BasePagination,
    ProductFilter: ProductFilter,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: [],
      filterCollectionId: [],
      filterMaterialId: [],
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
   


    products() {
      return this.productsData ?  this.productsData.items : []
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;

      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL + '/api/products', {
        params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorIds: this.filterColorId,
              materialIds: this.filterMaterialId,
              seasonIds: this.filterCollectionId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            }
      })
        .then(response => this.productsData = response.data)
        .catch(() => this.productsLoadingFailed = true)
        .then(()=> this.productsLoading = false)
      }, 0)
    }
  },
  created() {
    this.loadProducts()
  },
  watch: {
    
    page() {
      this.loadProducts();
    },
    filterCategoryId() {this.loadProducts();},
    filterCollectionId() {this.loadProducts();},
    filterPriceFrom(){this.loadProducts();},
    filterPriceTo() {this.loadProducts();},
    filterMaterialsId(){this.loadProducts();},
    filterColorId(){this.loadProducts();},
  }
}
</script>